<template>
  <div class="classificationItem">
    <section v-for="(item, index) in type" :key="'0' + index">
      <div v-if="item.mins.length!==0">
        <p>
          <img :src="item.icon" />
          {{ item.major }}
        </p>
        <ul class="CI-ul">
          <li
            v-for="(min, index) in item.mins"
            :key="'1' + index"
            @click="toTypeDetails(item.major, min, sex)"
          >
            {{ min }}
          </li>
        </ul>
      </div>
    </section>
  </div>
</template>

<script>
export default {
  props: ["type", "sex"],
  methods: {
    toTypeDetails(major, min, sex) {
      this.$router.push({
        path: "/typeDetails",
        query: {
          typeName: major,
          minName: min,
          sex,
          title: min,
          loading: true,
        },
      });
    },
  },
};
</script>

<style lang="less">
.classificationItem {
  padding:0 0 30px 15px;
  section {
    p {
      height: 40px;
      line-height: 40px;
      font-size: 18px;
      font-weight: 600;
      color: #33373d;
      font-family: KaiTi;
      position: relative;
      border-bottom: 1px solid rgba(0, 0, 0, 0.05);
      img{
        width: 25px;
        height: 25px;
        vertical-align: middle;
      }
    }
    .CI-ul {
      padding: 10px 0 0 10px;
      &::after {
        content: "";
        display: block;
        clear: both;
      }
      li {
        float: left;
        padding: 3px;
        border: 1px solid #3333338c;
        color: #333;
        border-radius: 15px;
        font-size: 13px;
        margin:0 10px 5px 0;
      }
    }
  }
}
</style>