<template>
  <div class="classification">
    <BgMask color="rgb(154,160,180)" />
    <HeaderNav title="分类" />
    <van-tabs v-model="active" animated swipeable>
      <van-tab title="男生">
        <van-loading
          type="spinner"
          color="#ccc"
          size="24px"
          v-if="TypeLoading"
          class="my-load"
        />
        <template v-else>
          <ClassificationItem :type="maleType" sex="male" />
        </template>
      </van-tab>
      <van-tab title="女生">
        <van-loading
          type="spinner"
          color="#ccc"
          size="24px"
          v-if="TypeLoading"
          class="my-load"
        />
        <template v-else>
          <ClassificationItem :type="femaleType" sex="female" />
        </template>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
// import { Toast } from "vant";
import HeaderNav from "@/components/HeaderNav.vue";
import BgMask from "@/components/BgMask.vue";
import ClassificationItem from "@/components/ClassificationItem.vue";
export default {
  components: {
    BgMask,
    HeaderNav,
    ClassificationItem,
  },
  data() {
    return {
      active: 0, //标签页切换的属性
      maleType: [],
      femaleType: [],
      TypeLoading: true,
      maleCategory: [
        "/category/icon01.png",
        "/category/icon02.png",
        "/category/icon03.png",
        "/category/icon04.png",
        "/category/icon05.png",
        "/category/icon06.png",
        "/category/icon07.png",
        "/category/icon08.png",
        "/category/icon09.png",
        "/category/icon10.png",
        "/category/icon11.png",
        "/category/icon12.png",
        "/category/icon13.png",
        "/category/icon14.png",
      ],
      femaleCategory: [
        "/category/icon14.png",
        "/category/icon15.png",
        "/category/icon16.png",
        "/category/icon17.png",
        "/category/icon18.png",
        "/category/icon19.png",
        "/category/icon20.png",
        "/category/icon21.png",
        "/category/icon22.png",
        "/category/icon13.png",
      ],
    };
  },
  created() {
    document.documentElement.scrollTop = 0;
    this.searchType();
  },
  // activated() {
  //   document.documentElement.scrollTop = 0;
  // },
  methods: {
    // onClick(name, title) {
    //   Toast(title);
    // },
    searchType() {
      this.TypeLoading = true;
      this.axios("/sub-categories")
        .then((res) => {
          // console.log(res.data);
          this.maleType = res.data.male.map((item, index) => {
            return {
              major: item.major,
              mins: item.mins,
              icon: this.maleCategory[index],
            };
          });
          this.femaleType = res.data.female.map((item, index) => {
            return {
              major: item.major,
              mins: item.mins,
              icon: this.femaleCategory[index],
            };
          });
        })
        .finally(() => {
          this.TypeLoading = false;
        });
    },
  },
};
</script>

<style lang="less">
.classification {
  padding: 0 10px;
  box-sizing: border-box;

  .van-tabs {
    background: #fff;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    margin-bottom: 80px;
    min-height: 60vh;
    .van-tabs__wrap {
      border-bottom: 2px solid rgba(0, 0, 0, 0.15);
      border-radius: 10px;
      .van-tabs__line {
        background-color: #f2d873;
      }
    }
    .van-tab__pane {
      // height: 75vh;
      .my-load {
        margin: 10px auto;
        width: 50px;
      }
    }
  }
}
</style>